
import classnames from 'classnames';
import bem from 'easy-bem';
import draggable from '../../mixins/draggable.js';
import { LineWrapper } from '../service';

const cn = bem('vue-default-line');

export default {
	name: 'SimpleLine',
	components: {
		LineWrapper
	},
	props: {
		classname: {
			type: String
		},
		hoverClassname: {
			type: String
		},
		position: {
			type: String
		}
	},
	data() {
		return {
			hover: false
		};
	},
	computed: {
		classnames() {
			return {
				default: classnames(
					cn({ [this.position]: true }),
					this.classname,
					this.hover && this.hoverClassname
				)
			};
		}
	},
	methods: {
		onDrag(dragEvent) {
			this.$emit('drag', dragEvent);
		},
		onEnter() {
			this.hover = true;
		},
		onLeave() {
			this.hover = false;
		}
	}
};
