
import classnames from 'classnames';
import bem from 'easy-bem';
import draggable from '../../mixins/draggable.js';
import { HandlerWrapper } from '../service';

const cn = bem('vue-square-handler');

export default {
	name: 'SimpleHandler',
	components: {
		HandlerWrapper
	},
	props: {
		classname: {
			type: String
		},
		hoverClassname: {
			type: String
		},
		horizontalPosition: {
			type: String
		},
		verticalPosition: {
			type: String
		}
	},
	computed: {
		classnames() {
			return {
				default: classnames(
					cn({ [this.position]: true }),
					this.classname,
					this.hover && this.hoverClassname
				)
			};
		}
	},
	methods: {
		onDrag(dragEvent) {
			this.$emit('drag', dragEvent);
		},
		onEnter() {
			this.hover = true;
		},
		onLeave() {
			this.hover = false;
		}
	}
};
